body {
margin: 0;
font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
sans-serif;
-webkit-font-smoothing: antialiased;
-moz-osx-font-smoothing: grayscale;

}

code {
font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
monospace;
}

.g-card {
margin: 20px;
}

.g-card-image {
align-items: center;
box-shadow: 
12px 12px 16px 0 rgba(0, 0, 0, 0.25),
-8px -8px 12px 0 rgba(255, 255, 255, 0.3);
border-radius: 50px;
display: flex;
height: 400px;
justify-content: center;
margin-right: 0rem;
width: 400px;
}

.g-card-info {
margin-top:  10px;
min-height: 10px;
}


.g-card-title {
font-size: 24px;
margin: 0px;
}

.g-card-sub-title {
font-size: 16px;
margin: 0px;
}


.success-msg{

color: #57A773;
padding: 10px 15px;

}


.err-msg{

color: #EE6352;
padding: 10px 15px;

}